import React, {useState, useEffect, useMemo} from "react";
import {Container, Pagination} from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';
import TableBody from "./components/TableBody/TableBody";
import Loader from "./components/Loader/Loader";
import UseServerData from "./components/Hooks/UseServerData";
import Paginator from "./components/Paginator/Paginator";

function App() {
    const [pageActive, setPageActive] = useState(0)
    const [rowsLimit, setRowsLimit] = useState(0)
    const [pageCount, setPageCount] = useState(0)
    const url = useMemo(() => {
        return "http://127.0.0.1:8000/api/rows/?offset=" + (pageActive * rowsLimit)
    }, [pageActive])
    const [{rows, isLoading}, getRows] = UseServerData(url)


    useEffect(() => {
        if (isLoading) {
            setRowsLimit(rows.results.length)
            setPageCount(Math.ceil(rows.count / rowsLimit))
        }
    })

    const pageItems = useMemo(() => {
        let pageItems = []
        for (let i = 0; i < pageCount; i++) {
            pageItems.push(
                <Pagination.Item key={i} active={i === pageActive} onClick={() => setPageActive(i)}>
                    {i + 1}
                </Pagination.Item>
            )
        }
        return pageItems
    }, [pageActive, isLoading])

    return(
        <Container>
            {isLoading ? <TableBody rows={rows}/> : <Loader/>}
            <Paginator items={pageItems}/>
        </Container>
    );
}

export default App;
